.card {
    display: grid;
    grid-template-areas:
        "imagem pet-infos"
        "imagem contato"
        "imagem .";
    grid-template-columns: 45% 55%;
    column-gap: 1rem;

    padding: 1rem 1.5rem;

    background-color: var(--cor-quinaria);

    color: var(--cor-hexanaria);
}

.card_image {
    grid-area: imagem;
    width: 100%;
    height: 100%;
}

.card_image img {
    width: inherit;
    height: inherit;
}

.card_infos {
    grid-area: pet-infos;
}

.card_infos__nome {
    margin-bottom: .5rem;

    font-weight: 600;
    color: var(--cor-primaria);
}

.card_infos__infos {
    margin-bottom: 1.875rem;

    font-size: .875rem;
    line-height: 1.25rem;
}

.card_contato__endereco {
    font-size: .75rem;

    margin-bottom: .5rem;
}

.card_contato__link {
    display: flex;
    align-items: flex-start;

    font-size: .625rem;
}

.card_contato__link::before {
    content: url(/public/assets/imagens/mensagem-icon-2.png);
    align-self: flex-end;

    display: inline-block;
    width: 1.125rem;
    width: 1.450rem;
    margin-right: .25rem;
}
